  main.variable(observer("weatherMonthly")).define("weatherMonthly", ["months", "weather", "d3"], function (months, weather, d3)
  {
    // compute monthly average temps
    return months.map((month, monthIdx) => {
      const monthDays = weather.filter((d) => d.date.month() === monthIdx);
      return {
        monthIdx,
        month: month.month,
        sunrise: d3.mean(monthDays, (d) => d.sunRise),
        sunset: d3.mean(monthDays, (d) => d.sunSet),
        temp: d3.mean(monthDays, (d) => d.temp) };

    });


  });

  main.variable(observer("months")).define("months", ["dayjs", "weather"], function (dayjs, weather)
  {
    // return array of month objects
    let monthNames = [
    'January', 'February', 'March', 'April', 'May', 'June',
    'July', 'August', 'September', 'October', 'November', 'December'];

    return monthNames.map((month, i) => {
      const dateObj = dayjs(`${month} 01 2019`); // not a leap year!
      const dayIdx = dateObj.dayOfYear() - 1;

      return {
        month: month,
        monthIdx: i,
        date: dateObj,
        sunrise: weather.find((d) => d.dayIdx === dayIdx).sunRise,
        sunset: weather.find((d) => d.dayIdx === dayIdx).sunSet,
        daysInMonth: dateObj.daysInMonth(),
        dayIdx: dayIdx,
        dateStr: dateObj.format('MMM') };

    });
  });

