  main.variable(observer("test")).define("test", ["generateKernel", "ts", "Plot"], function (generateKernel, ts, Plot)
  {
    let kernel = generateKernel(ts, 12, 9, 5);

    let shifted = ts.map((t, i) => {
      // shift the kernel based on offset from noon. Use modulo to wrap around midnight
      return kernel[(ts.length - 10 + i) % ts.length];
    });

    return Plot.plot({
      height: 100,
      marks: [
      Plot.line(ts.map((t, i) => [t, shifted[i]]), { curve: 'basis' })] });


  });

  main.variable(observer("generateKernel")).define("generateKernel", function () {return (
      (timepts, mean, width, amp) => {
        /* return the Gaussian kernel defined by mean, std, and amp, over the given timepts range
           mean: center point of kernel
           width:  
        */
        let std = width / 2 * .25;
        let kernel = timepts.map((x) => {
          return amp * Math.exp(-Math.pow(x - mean, 2) / Math.pow(2 * std, 2));
        });
        return kernel;
      });
  });
  main.variable(observer("tsToFraction")).define("tsToFraction", ["d3"], function (d3) {return (
      (timeString) => {
        // convert time string like "5:30:32 am" to fractional representation, like 5.5
        let timeParse = d3.timeParse('%I:%M:%S %p');
        let dt = timeParse(timeString);
        return dt.getHours() + dt.getMinutes() / 60;
      });
  });
