import define1 from "https://api.observablehq.com/d/75836c71d23e67a3.js?v=3";
import define2 from "https://api.observablehq.com/@jashkenas/inputs.js?v=3";
import define3 from "https://api.observablehq.com/@d3/color-legend.js?v=3";
import define4 from "https://api.observablehq.com/@shaunlebron/color-picker.js?v=3";

export default function define(runtime, observer) {
  const main = runtime.module();
  main.variable(observer()).define(["md"], function (md) {return (
      md`# Bill comparison - Daily Radial

Prototype for bill comparison tool - Texas Solar Homeowners & Savings Report visualization`);
  });
  main.variable(observer()).define(["md"], function (md) {return (
      md`## Usage parameters`);
  });
  main.variable(observer("viewof homeSizeSlider")).define("viewof homeSizeSlider", ["slider"], function (slider)
  {
    const sizes = [
    '1br house',
    '2br house',
    '3br house',
    '4+br house'];

    return slider({
      title: "Home size",
      value: 0,
      min: 0,
      max: sizes.length - 1,
      display: (v) => sizes[Math.floor(v)] });

  });

  main.variable(observer("homeSizeSlider")).define("homeSizeSlider", ["Generators", "viewof homeSizeSlider"], (G, _) => G.input(_));
  main.variable(observer("viewof peakTimeSlider")).define("viewof peakTimeSlider", ["slider", "dayjs"], function (slider, dayjs)
  {
    const covertFractionToTime = (fract) => {
      const hours = parseInt(fract);
      const mins = Math.round(fract % 1 * 60);
      return `${hours}:${mins < 10 ? '0' + mins : mins}`;
    };
    return slider({
      title: "Peak energy usage",
      value: 0,
      min: 5,
      max: 14,
      step: 1,
      display: (v) => `${dayjs().hour(12 + v).format('h a')}` });

  });

  main.variable(observer("peakTimeSlider")).define("peakTimeSlider", ["Generators", "viewof peakTimeSlider"], (G, _) => G.input(_));
  main.variable(observer()).define(["md"], function (md) {return (
      md`## Generation parameters`);
  });
  main.variable(observer("viewof panelSizeSlider")).define("viewof panelSizeSlider", ["slider"], function (slider)
  {
    return slider({
      title: "Panel size (kW)",
      value: 6,
      display: (v) => `${v} kW`,
      step: 1,
      min: 0,
      max: 12 });

  });

  main.variable(observer("panelSizeSlider")).define("panelSizeSlider", ["Generators", "viewof panelSizeSlider"], (G, _) => G.input(_));
  main.variable(observer()).define(["md"], function (md) {return (
      md`---`);
  });
  main.variable(observer("dailyChart")).define("dailyChart", ["netHeatmap", "meanMonthlyData", "monthlyData", "d3", "width", "height", "innerRadiusSlider", "outerRadiusSlider", "deg2rad", "wedgeAngleSlider", "dayjs", "colorScale"], function (netHeatmap, meanMonthlyData, monthlyData, d3, width, height, innerRadiusSlider, outerRadiusSlider, deg2rad, wedgeAngleSlider, dayjs, colorScale)
  {
    // get the data
    let chartData = netHeatmap === 999 ?
    meanMonthlyData :
    monthlyData.filter((d) => d.monthIdx === netHeatmap.monthIdx);


    // --- Chart container
    const svg = d3.create('svg').
    attr("viewBox", `${-width / 2} ${-height / 2} ${width} ${height / 2}`).
    style("width", "100%").
    style("height", "auto").
    style("font", "10px sans-serif");

    const arrowPoints = [[0, 0], [0, 10], [10, 5]];
    const mBoxW = 10;
    const mBoxH = 10;
    svg.
    append('defs').
    append('marker').
    attr('id', 'arrow').
    attr('viewBox', [0, 0, mBoxW, mBoxH]).
    attr('refX', mBoxW / 2).
    attr('refY', mBoxH / 2).
    attr('markerWidth', mBoxW / 2).
    attr('markerHeight', mBoxH / 2).
    attr('orient', 'auto-start-reverse').
    append('path').
    attr('d', d3.line()(arrowPoints)).
    attr('stroke', 'none').
    attr('fill', '#777');

    const innerRadius = innerRadiusSlider;
    const outerRadius = outerRadiusSlider;
    const polar2Cart = (r, theta) => {
      // convert the given polar coords to x,y for the chart
      theta = theta - Math.PI / 2 + xScale.bandwidth() / 2; // rotate to fit the chart
      return {
        x: r * Math.cos(theta),
        y: r * Math.sin(theta) };

    };

    // --- Scales
    const angles = [-deg2rad(wedgeAngleSlider), deg2rad(wedgeAngleSlider)];
    const xScale = d3.scaleBand().
    domain(chartData.map((d) => d.ts)).
    range(angles).
    align(0);
    const yScale = d3.scaleLinear().
    domain([-6, 6]).
    range([innerRadius, outerRadius]);
    const netExtent = d3.extent(chartData, (d) => d.net);
    const colorMin = netExtent[0] < 0 ? netExtent[0] : 0;


    // --- usage/consumption bars

    const usageArc = d3.arc().
    innerRadius((d) => yScale(0)).
    outerRadius((d) => yScale(d.usage)).
    startAngle((d) => xScale(d.ts)).
    endAngle((d) => xScale(d.ts) + xScale.bandwidth()).
    padAngle(0.015);
    const usageG = svg.append('g');
    usageG.selectAll('path').
    data(chartData).
    join('path').
    attr('d', usageArc).
    attr('fill', '#ccc').
    attr('fill-opacity', .4).
    attr('stroke', '#ccc');

    const genArc = d3.arc().
    innerRadius((d) => yScale(-d.generation)).
    outerRadius((d) => yScale(0)).
    startAngle((d) => xScale(d.ts)).
    endAngle((d) => xScale(d.ts) + xScale.bandwidth()).
    padAngle(0.015);
    const genG = svg.append('g');
    genG.selectAll('path').
    data(chartData).
    join('path').
    attr('d', genArc).
    attr('fill', '#999').
    attr('fill-opacity', .4).
    attr('stroke', '#999');

    // --- Axes
    const xAxis = (g) => g.
    attr('text-anchor', 'middle').
    call((g) => g.selectAll('g').
    data(chartData).
    join('g').
    attr("transform", (d) => {
      const coords = polar2Cart(yScale(-d.generation - 2.5), xScale(d.ts) - xScale.bandwidth() / 2);
      return `translate(${coords.x},${coords.y})`;
    }).
    call((g) => g.append("text").
    text((d) => {
      if (d.ts === 12) {
        return 'noon';
      } else {
        return d.ts % 3 === 0 ? dayjs().hour(d.ts).format('h a') : null;
      }
    }).
    style('fill', '#777').
    style('font-size', '14px')));


    svg.append('g').
    call(xAxis);

    const yAxisLine = d3.arc().
    innerRadius((d) => yScale(d)).
    outerRadius((d) => yScale(d) + 1).
    startAngle(angles[0]).
    endAngle(angles[1]);
    const yAxis = (g) => g.
    attr('text-anchor', 'middle').
    call((g) => g.selectAll('g').
    data([-6, 0, 6]).
    join('g').
    call((g) => g.append('path').
    attr('d', yAxisLine).
    attr('fill', (d) => d === 0 ? 'black' : "none").
    attr('stroke-dasharray', '2').
    attr('stroke', (d) => d === 0 ? "none" : "#ddd")));

    svg.append('g').
    call(yAxis);

    // --- Net line/dots
    const netG = svg.append('g').attr('class', 'net-line');
    const netLine = d3.line().
    x((d) => polar2Cart(yScale(d.net), xScale(d.ts)).x).
    y((d) => polar2Cart(yScale(d.net), xScale(d.ts)).y);
    // net diff line
    netG.selectAll('line').
    data(chartData).
    join('line').
    attr('class', 'diff-line').
    attr('x1', (d) => polar2Cart(yScale(d.usage), xScale(d.ts)).x).
    attr('y1', (d) => polar2Cart(yScale(d.usage), xScale(d.ts)).y).
    attr('x2', (d) => {
      return d.usage - d.net > 1.6 ?
      polar2Cart(yScale(d.net + 1.6), xScale(d.ts)).x :
      polar2Cart(yScale(d.net), xScale(d.ts)).x;
    }).
    attr('y2', (d) => {
      return d.usage - d.net > 1.6 ?
      polar2Cart(yScale(d.net + 1.6), xScale(d.ts)).y :
      polar2Cart(yScale(d.net), xScale(d.ts)).y;
    }).
    attr('stroke-width', 1).
    attr('stroke-dasharray', '3 1').
    attr('stroke', '#777').
    attr('marker-end', (d) => {
      if (d.usage - d.net > 1.6) {
        return 'url(#arrow)';
      } else {
        return null;
      }
    });

    // connecting hours line
    netG.append('path').
    attr('d', netLine(chartData)).
    attr('fill', "none").
    attr('stroke', 'black').
    attr('stroke-dasharray', '6 4');
    // dots
    netG.selectAll('circle').
    data(chartData).
    join('circle').
    attr('cx', (d) => polar2Cart(yScale(d.net), xScale(d.ts)).x).
    attr('cy', (d) => polar2Cart(yScale(d.net), xScale(d.ts)).y).
    attr('r', 10).
    attr('stroke', 'black').
    attr('fill', (d) => colorScale(d.net));


    return svg.node();
  });

  main.variable(observer()).define(["md"], function (md) {return (
      md`---`);
  });
  main.variable(observer("viewof netHeatmap")).define("viewof netHeatmap", ["d3", "width", "ts", "months", "monthlyData", "colorScale"], function (d3, width, ts, months, monthlyData, colorScale)
  {
    const height = 250;
    const svg = d3.create('svg').
    attr("viewBox", [0, 0, width, height]);

    const margin = {
      left: 150,
      right: 120,
      top: 0,
      bottom: 0 };


    const xScale = d3.scaleBand().
    domain(d3.range(ts.length)).
    range([margin.left, width - margin.left - margin.right]).
    paddingInner(.05).
    align(0.5);

    const yScale = d3.scaleBand().
    domain(d3.range(months.length)).
    range([margin.top, height - margin.bottom]).
    paddingOuter(0).
    paddingInner(0.05).
    align(0.5);

    function scaleBandInvert(scale) {
      var domain = scale.domain();
      var paddingOuter = scale(domain[0]);
      var eachBand = scale.step();
      return function (value) {
        var index = Math.floor((value - paddingOuter) / eachBand);
        return domain[Math.max(0, Math.min(index, domain.length - 1))];
      };
    }

    // axes
    const yAxis = (g) => g.
    attr("transform", `translate(${margin.left - 20}, 0)`).
    call(d3.axisLeft(
    d3.scaleBand().
    domain(months.map((d) => d.dateStr)).
    range([margin.top, height - margin.bottom])));

    svg.append('g').selectAll('text').
    data(months).
    join('text').
    attr('text-anchor', 'end').
    attr('alignment-baseline', 'middle').
    attr('x', margin.left - 26).
    attr('y', (d) => yScale(d.monthIdx) + 13).
    style('font', '14px sans-serif').
    text((d) => d.dateStr);

    svg.append('g').selectAll('line').
    data(months).
    join('line').
    attr('class', 'month-line').
    attr('x1', margin.left - 20).
    attr('x2', width - margin.left - margin.right).
    attr('y1', (d) => yScale(d.monthIdx) + yScale.bandwidth() / 2).
    attr('y2', (d) => yScale(d.monthIdx) + yScale.bandwidth() / 2).
    style('stroke', '#777');


    //const netExtent = d3.extent(monthlyData, d => d.net);
    const netExtent = [-5, 5];
    const colorMin = netExtent[0] < 0 ? netExtent[0] : 0;

    // background to stop mouseover flickering
    const circleG = svg.append('g');

    // create heatmap 
    circleG.
    selectAll('circle').
    data(monthlyData).
    join('circle').
    attr('cy', (d) => yScale(d.monthIdx) + yScale.bandwidth() / 2).
    attr('cx', (d) => xScale(d.ts)).
    attr('r', 9).
    attr('fill', (d) => colorScale(d.net)).
    style('stroke-width', 1).
    style('stroke', '#777').
    attr('class', (d) => `heatmap-rect month-${d.monthIdx} ts-${d.ts}`);

    circleG.
    on('mouseover', (event, d) => {
      let x = d3.pointer(event)[0];
      let y = d3.pointer(event)[1];
      let monthIdx = scaleBandInvert(yScale)(y);
      let dayIdx = months[monthIdx].dayIdx;
      let tsIdx = scaleBandInvert(xScale)(x);
      d3.selectAll('.heatmap-rect').
      style('fill-opacity', (d) => d.monthIdx === monthIdx ? 1 : .13).
      style('stroke', (d) => d.monthIdx === monthIdx ? '#222' : '#ccc').
      style('stroke-width', (d) => d.monthIdx === monthIdx & d.ts === tsIdx ? 4 : 1);
      d3.selectAll('.month-line').
      attr('visibility', (d) => d.monthIdx === monthIdx ? 'visible' : 'hidden');
      svg.property('value', { monthIdx, tsIdx }).dispatch('input');
    }).
    on('mouseout', () => {
      d3.selectAll('.heatmap-rect').
      style('fill-opacity', 1).
      style('stroke', '#777').
      style('stroke-width', 1);
      d3.selectAll('.month-line').
      attr('visibility', 'visible');
      svg.property('value', 999).dispatch('input');
    });

    svg.property('value', 999).dispatch('input');
    return svg.node();
  });

  main.variable(observer("netHeatmap")).define("netHeatmap", ["Generators", "viewof netHeatmap"], (G, _) => G.input(_));
  main.variable(observer("netLegend")).define("netLegend", ["legend", "colorScale", "legendWidth", "legendLabels"], function (legend, colorScale, legendWidth, legendLabels) {return (
      legend({
        color: colorScale,
        width: legendWidth,
        ticks: 3,
        tickSize: 4,
        tickFormat: (d, i) => legendLabels[i],
        marginLeft: 20,
        title: "Net Energy (kWh)" }));

  });
  main.variable(observer("viewof usageColor")).define("viewof usageColor", ["colorPicker"], function (colorPicker) {return (
      colorPicker('#2474ad'));
  });
  main.variable(observer("usageColor")).define("usageColor", ["Generators", "viewof usageColor"], (G, _) => G.input(_));
  main.variable(observer("viewof generationColor")).define("viewof generationColor", ["colorPicker"], function (colorPicker) {return (
      colorPicker('#ffc038'));
  });
  main.variable(observer("generationColor")).define("generationColor", ["Generators", "viewof generationColor"], (G, _) => G.input(_));
  main.variable(observer("legendWidth")).define("legendWidth", function () {return (
      200);
  });
  main.variable(observer("legendLabels")).define("legendLabels", function () {return (
      ['importing', '⟵ 0 ⟶', 'exporting']);
  });
  main.variable(observer("colorScale")).define("colorScale", ["d3", "usageColor", "generationColor"], function (d3, usageColor, generationColor) {return (
      d3.scaleDiverging((t) => d3.interpolateRgb(1 - t)).
      domain([5, 0, -5]).
      range([usageColor, '#fff', generationColor]));
  });
  main.variable(observer()).define(["md"], function (md) {return (
      md`## Data`);
  });
  main.variable(observer("height")).define("height", function () {return (
      600);
  });
  main.variable(observer("monthlyData")).define("monthlyData", ["generateMonthlyData", "panelSizeSlider", "homeSizeSlider", "peakTimeSlider"], function (generateMonthlyData, panelSizeSlider, homeSizeSlider, peakTimeSlider) {return (
      generateMonthlyData(panelSizeSlider, homeSizeSlider, peakTimeSlider));
  });
  main.variable(observer("meanMonthlyData")).define("meanMonthlyData", ["d3", "monthlyData"], function (d3, monthlyData)
  {
    // compute mean across days of usage/generation and net at each timestamp
    const mean = d3.rollup(
    monthlyData,
    (v) => {
      const meanUsage = d3.mean(v, (v) => v.usage);
      const meanGeneration = d3.mean(v, (v) => v.generation);
      return {
        ts: v[0].ts,
        usage: meanUsage,
        generation: meanGeneration,
        net: meanUsage - meanGeneration };

    },
    (d) => d.ts);
    return Array.from(mean).map((d) => d[1]);
  });

  main.variable(observer("monthlyBills")).define("monthlyBills", ["months", "monthlyData", "generateDailyBills", "plans", "d3"], function (months, monthlyData, generateDailyBills, plans, d3)
  {
    /* return an array of bills for each month
      Each month in the array will have a child array called "plans" that 
      contains the monthly details for each plan
    */
    return months.map((month) => {
      // get the typical daily ts data for this month
      const monthData = monthlyData.filter((d) => d.monthIdx === month.monthIdx);

      //generate a typical daily bill for each plan for this month
      let typicalBills = generateDailyBills(monthData, plans);

      // find cheapest bill
      const cheapestIdx = d3.minIndex(typicalBills, (d) => d.dailyBill);
      typicalBills = typicalBills.map((plan, i) => ({
        ...plan,
        isCheapest: i === cheapestIdx,
        monthlyBill: plan.dailyBill * month.daysInMonth }));


      return {
        month: month.month,
        monthIdx: month.monthIdx,
        date: month.date,
        plans: typicalBills };

    });
  });

  main.variable(observer("yearlyBills")).define("yearlyBills", ["generateDailyBills", "meanMonthlyData", "plans", "monthlyBills", "d3"], function (generateDailyBills, meanMonthlyData, plans, monthlyBills, d3)
  {
    /* calculate the yearly bills for each plan by summing the monthly bill for each plan.
      Also store a bill curve that represents the mean of all monthly bill curves. 
    */
    // generate bill curves for each plan using the meanMonthlyBill
    let billCurves = generateDailyBills(meanMonthlyData, plans);


    let planKeys = Object.keys(plans);
    let yearlySums = planKeys.map((planKey) => {
      // planKey = 'netMetering'
      const thisPlanMonthlyBills = monthlyBills.
      map((d) => d.plans.find((dd) => dd.plan === planKey));
      return {
        plan: planKey,
        billCurve: billCurves.find((d) => d.plan === planKey).billCurve,
        yearlyBill: d3.sum(thisPlanMonthlyBills, (d) => d.monthlyBill) };

    });

    // figure out which is the cheapest
    const cheapestIdx = d3.minIndex(yearlySums, (d) => d.yearlyBill);
    return yearlySums.map((plan, i) => ({ ...plan, isCheapest: i === cheapestIdx }));
  });

  main.variable(observer()).define(["md"], function (md) {return (
      md`### Unused`);
  });
  main.variable(observer("dailyData")).define("dailyData", ["generateDailyData", "panelSizeSlider", "homeSizeSlider", "peakTimeSlider"], function (generateDailyData, panelSizeSlider, homeSizeSlider, peakTimeSlider) {return (
      generateDailyData(panelSizeSlider, homeSizeSlider, peakTimeSlider));
  });
  main.variable(observer("meanDailyData")).define("meanDailyData", ["d3", "dailyData"], function (d3, dailyData)
  {
    // compute mean across days of usage/generation and net at each timestamp
    const mean = d3.rollup(
    dailyData,
    (v) => {
      const meanUsage = d3.mean(v, (v) => v.usage);
      const meanGeneration = d3.mean(v, (v) => v.generation);
      return {
        ts: v[0].ts,
        usage: meanUsage,
        generation: meanGeneration,
        net: meanUsage - meanGeneration };

    },
    (d) => d.ts);
    return Array.from(mean).map((d) => d[1]);
  });

  main.variable(observer("viewof innerRadiusSlider")).define("viewof innerRadiusSlider", ["slider"], function (slider)
  {
    return slider({
      title: "innerRadius",
      value: 150,
      step: 1,
      min: 50,
      max: 250 });

  });

  main.variable(observer("innerRadiusSlider")).define("innerRadiusSlider", ["Generators", "viewof innerRadiusSlider"], (G, _) => G.input(_));
  main.variable(observer("viewof outerRadiusSlider")).define("viewof outerRadiusSlider", ["slider"], function (slider)
  {
    return slider({
      title: "outerRadius",
      value: 270,
      step: 1,
      min: 150,
      max: 350 });

  });

  main.variable(observer("outerRadiusSlider")).define("outerRadiusSlider", ["Generators", "viewof outerRadiusSlider"], (G, _) => G.input(_));
  main.variable(observer("viewof wedgeAngleSlider")).define("viewof wedgeAngleSlider", ["slider"], function (slider)
  {
    return slider({
      title: "wedgeAngle",
      value: 90,
      step: 1,
      min: 0,
      max: 180 });

  });

  main.variable(observer("wedgeAngleSlider")).define("wedgeAngleSlider", ["Generators", "viewof wedgeAngleSlider"], (G, _) => G.input(_));
  main.variable(observer()).define(["md"], function (md) {return (
      md`## Appendix`);
  });
  main.variable(observer("deg2rad")).define("deg2rad", function () {return (
      (deg) => {
        return deg * Math.PI / 180;
      });
  });
  main.variable(observer("calculateMeanCurve")).define("calculateMeanCurve", ["ts", "d3"], function (ts, d3) {return (
      (yearlyCurves) => {
        // collapse across days to get mean import and SEM at each point throughout the day
        return ts.map((t, i) => {
          const mean = d3.mean(yearlyCurves, (d) => d.curve[i]);
          const nSamples = yearlyCurves.length;
          let sem = d3.deviation(yearlyCurves, (d) => d.curve[i]) / Math.sqrt(nSamples);

          return {
            ts: t,
            energy: mean,
            sem: sem,
            upper: mean + sem * 30,
            lower: mean - sem * 30 };

        });
      });
  });
  main.variable(observer("formatDate")).define("formatDate", ["d3"], function (d3) {return (
      d3.timeFormat('%B %d'));
  });
  const child1 = runtime.module(define1);
  main.import("ts", child1);
  main.import("months", child1);
  main.import("generateDailyData", child1);
  main.import("generateMonthlyData", child1);
  main.import("generateDailyBills", child1);
  main.import("plans", child1);
  const child2 = runtime.module(define2);
  main.import("slider", child2);
  const child3 = runtime.module(define3);
  main.import("legend", child3);
  const child4 = runtime.module(define4);
  main.import("colorPicker", child4);
  main.variable(observer("d3")).define("d3", ["require"], function (require) {return (
      require('d3@6'));
  });
  main.variable(observer("dayjs")).define("dayjs", ["require"], function (require) {return (
      require('dayjs'));
  });
  main.variable(observer("dayOfYear")).define("dayOfYear", ["require"], function (require) {return (
      require('dayjs/plugin/dayOfYear'));
  });
  main.variable(observer()).define(["dayjs", "dayOfYear"], function (dayjs, dayOfYear) {return (
      dayjs.extend(dayOfYear));
  });
  main.variable(observer("textures")).define("textures", ["require"], function (require) {return (
      require('textures'));
  });
  return main;
}